import React, { useState } from 'react';
import styles from './toggle.module.css';

const Toggle = ( props ) => {

    const [isOpen, setOpen] = useState(false);

    return (
        <div className="py-2">
            <button className={styles.button} onClick={() => setOpen(!isOpen)}>
                <p className="flex-1">{props.title}</p>
            </button>
            { isOpen && ( <div className=" font-light max-w-2xl px-2 pt-2 pb-4 text-gray-800 text-lg">{ props.children }</div> ) }
        </div>
    )

}

export default Toggle;